/* App.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #4a6fa5;
  --primary-dark: #3a5a80;
  --success-color: #27ae60;
  --error-color: #e74c3c;
  --text-color: #2c3e50;
  --bg-color: #f5f7fa;
  --card-bg: #ffffff;
  --border-color: #e1e8ed;
  --shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
}

.quiz-container {
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: var(--shadow);
  width: 100%;
  max-width: 800px;
  overflow: hidden;
  animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.quiz-header {
  background: var(--primary-color);
  color: white;
  padding: 20px;
  text-align: center;
  position: relative;
}

.quiz-header h1 {
  margin-bottom: 10px;
  font-size: 1.8rem;
  font-weight: 600;
}

.quiz-info {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  opacity: 0.9;
  font-weight: 500;
}

.question-container {
  padding: 30px;
}

.question-text {
  font-size: 1.3rem;
  margin-bottom: 25px;
  color: var(--text-color);
  font-weight: 500;
  line-height: 1.4;
}

.options-container {
  margin-bottom: 25px;
}

.option {
  display: flex;
  align-items: center;
  padding: 15px;
  margin-bottom: 12px;
  border: 2px solid var(--border-color);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.option::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 0;
  background: var(--primary-color);
  opacity: 0.1;
  transition: width 0.2s ease;
}

.option:hover::before {
  width: 100%;
}

.option:hover {
  border-color: var(--primary-color);
  transform: translateY(-1px);
}

.option.selected {
  border-color: var(--primary-color);
  background-color: rgba(74, 111, 165, 0.05);
}

.option.correct {
  border-color: var(--success-color);
  background-color: rgba(39, 174, 96, 0.1);
  animation: pulse 0.5s ease;
}

.option.incorrect {
  border-color: var(--error-color);
  background-color: rgba(231, 76, 60, 0.1);
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
}

.option-letter {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  background: var(--primary-color);
  color: white;
  border-radius: 50%;
  margin-right: 15px;
  font-weight: bold;
  flex-shrink: 0;
  transition: background-color 0.2s ease;
}

.option.correct .option-letter {
  background: var(--success-color);
}

.option.incorrect .option-letter {
  background: var(--error-color);
}

.option-text {
  flex: 1;
  font-weight: 500;
}

.explanation {
  background: rgba(74, 111, 165, 0.05);
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  border-left: 4px solid var(--primary-color);
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from { opacity: 0; max-height: 0; }
  to { opacity: 1; max-height: 200px; }
}

.explanation h3 {
  margin-bottom: 10px;
  color: var(--text-color);
  font-size: 1.1rem;
}

.explanation p {
  color: var(--text-color);
  opacity: 0.9;
  line-height: 1.5;
}

.quiz-controls {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

.submit-btn, .next-btn, .restart-btn {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 12px 25px;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 140px;
}

.submit-btn:hover, .next-btn:hover, .restart-btn:hover {
  background: var(--primary-dark);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.submit-btn:disabled {
  background: #a0aec0;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.quiz-completed {
  text-align: center;
  padding: 40px;
  animation: zoomIn 0.5s ease;
}

@keyframes zoomIn {
  from { opacity: 0; transform: scale(0.9); }
  to { opacity: 1; transform: scale(1); }
}

.quiz-completed h2 {
  margin-bottom: 30px;
  color: var(--text-color);
  font-size: 2rem;
}

.score-card {
  margin-bottom: 30px;
  padding: 20px;
  background: rgba(74, 111, 165, 0.05);
  border-radius: 10px;
}

.score {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: var(--primary-color);
  font-weight: 600;
}

.percentage {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: var(--success-color);
}

.performance-message {
  font-size: 1.2rem;
  font-weight: 500;
  color: var(--text-color);
  margin-top: 10px;
}

.loading {
  text-align: center;
  padding: 40px;
  font-size: 1.2rem;
  color: var(--text-color);
}

.progress-bar {
  height: 6px;
  background: var(--border-color);
  width: 100%;
  position: relative;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-color), var(--primary-dark));
  transition: width 0.3s ease;
  position: relative;
}

.progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.2) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.2) 75%,
    transparent 75%,
    transparent
  );
  background-size: 20px 20px;
  animation: moveStripes 1s linear infinite;
}

@keyframes moveStripes {
  0% { background-position: 0 0; }
  100% { background-position: 20px 0; }
}

/* Responsive Design */
@media (max-width: 768px) {
  .app {
    padding: 10px;
  }
  
  .quiz-container {
    border-radius: 8px;
  }
  
  .question-container {
    padding: 20px;
  }
  
  .quiz-header {
    padding: 15px;
  }
  
  .quiz-header h1 {
    font-size: 1.5rem;
  }
  
  .question-text {
    font-size: 1.1rem;
  }
  
  .quiz-controls {
    flex-direction: column;
  }
  
  .submit-btn, .next-btn, .restart-btn {
    width: 100%;
    min-width: auto;
  }
  
  .option {
    padding: 12px;
  }
  
  .option-letter {
    width: 28px;
    height: 28px;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .quiz-completed h2 {
    font-size: 1.5rem;
  }
  
  .percentage {
    font-size: 2rem;
  }
  
  .score {
    font-size: 1.3rem;
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus styles for keyboard navigation */
.option:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
}

button:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
}